/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { useEffect } from '@wordpress/element';
import { useSelect } from '@wordpress/data';
import { registerBlockType } from '@wordpress/blocks';
import {
	InspectorControls,
	BlockControls,
	InnerBlocks,
	useBlockProps,
} from '@wordpress/block-editor';
import { PanelBody, TextControl, CheckboxControl } from '@wordpress/components';
import { useInnerBlocksProps } from '@compatible';

/**
 * @Internal dependencies
 */
import metadata from './block.json';
import blockIcon from './icon';
import example from './_example';
import { ArkbMarginControl, ArkbProOnly } from '@components';

const IS_PRO = !!window.arkbSettings?.pfkey || false;

/**
 * STEPブロック
 */
const blockName = 'ark-block-step';
registerBlockType(metadata.name, {
	title: __('Step', 'arkhe-blocks'),
	description: __('Create step-by-step content.', 'arkhe-blocks'),
	icon: blockIcon,
	example,
	styles: [
		{ name: 'default', label: __('Default', 'arkhe-blocks'), isDefault: true },
		{ name: 'big', label: __('Big', 'arkhe-blocks') },
		{ name: 'card', label: __('Card', 'arkhe-blocks') },
	],
	edit: ({ attributes, setAttributes, isSelected, clientId }) => {
		const { stepLabel, startNum, isHideNum, isHideLabel } = attributes;

		// デフォルトクラスを強制セット
		useEffect(() => {
			if (!attributes.className) {
				setAttributes({ className: 'is-style-default' });
			}
		}, [attributes.className]);

		// インナーブロックが選択されているかどうか. deep:false で直下だけチェック
		const isChildSelected = useSelect(
			(select) => select('core/block-editor').hasSelectedInnerBlock(clientId, false),
			[clientId]
		);

		// ブロックProps
		const blockProps = useBlockProps({
			className: `${blockName} ark-has-guide ark-has-appender`,
			style: 1 < startNum ? { counterReset: `step ${startNum - 1}` } : null,
		});
		const innerBlocksProps = useInnerBlocksProps(blockProps, {
			allowedBlocks: ['arkhe-blocks/step-item'],
			template: [['arkhe-blocks/step-item'], ['arkhe-blocks/step-item']],
			templateLock: false,
			// renderAppender: InnerBlocks.ButtonBlockAppender,
			renderAppender: isSelected || isChildSelected ? InnerBlocks.ButtonBlockAppender : false,
		});

		return (
			<>
				<BlockControls>
					<ArkbMarginControl {...{ className: attributes.className, setAttributes }} />
				</BlockControls>
				<InspectorControls>
					<PanelBody title={__('Label settings', 'arkhe-blocks')}>
						<CheckboxControl
							label={__('Hide the label', 'arkhe-blocks')}
							checked={isHideLabel}
							onChange={(val) => setAttributes({ isHideLabel: val })}
						/>
						<div data-ark-disabled={isHideLabel}>
							<TextControl
								label={__('Text of "STEP" part', 'arkhe-blocks')}
								value={stepLabel}
								onChange={(val) => {
									setAttributes({ stepLabel: val });
								}}
							/>
						</div>
					</PanelBody>
					<PanelBody title={__('Number settings', 'arkhe-blocks')}>
						<ArkbProOnly>
							<CheckboxControl
								label={__('Hide the number', 'arkhe-blocks')}
								checked={isHideNum}
								onChange={(val) => {
									if (!IS_PRO) return;
									setAttributes({ isHideNum: val });
								}}
							/>
							<div data-ark-disabled={isHideNum}>
								<TextControl
									label={__('Start number', 'arkhe-blocks')}
									value={startNum}
									type='number'
									onChange={(val) => {
										if (!IS_PRO) return;
										// typeがnumberなので、intに変換してから保存！
										setAttributes({ startNum: parseInt(val) });
									}}
								/>
							</div>
						</ArkbProOnly>
					</PanelBody>
				</InspectorControls>
				<div {...innerBlocksProps} />
			</>
		);
	},

	save: ({ attributes }) => {
		const startNum = parseInt(attributes.startNum);

		const blockProps = useBlockProps.save({
			className: `${blockName}`,
			style: 1 < startNum ? { counterReset: `step ${startNum - 1}` } : null,
		});
		return (
			<div {...blockProps}>
				<InnerBlocks.Content />
			</div>
		);
	},
});
